@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
    padding: 4rem 2rem;
}

.form-control {
    margin: 10px 0px;
}

.row {
    flex-wrap: none!important;
    margin-right: 0!important;
    margin-left: 0!important;
    margin-bottom: 40px;
    background: rgba(255, 255, 255, 0.975);
    padding: 50px;
    border-radius: 5px;
}

h1 {
    font-size: 30px!important;
}

.input {
    outline: none;
}


.message {
    border-bottom: 1px dotted grey;
}
.players-container {
    background: #f7f7f7;
    border-radius: 5px;
    padding: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
}
.serverRes {
    background-color: rgba(31, 127, 244, 0.872);
    background-color: #f7f7f7;
    border-radius: 5px;
    padding: 10px;
    margin-top: 20px;
}

[type=button] {
    margin-right: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.messages-container {
    background: #f7f7f7;
    border-radius: 5px;
    padding: 20px;
    margin-bottom: 20px;
}

.p-players {
    border-bottom: 1px dotted grey;
}

.errorMsg{
    background-color: #f7f7f7;
    padding: 20px;
    border-radius: 5px;
}

.wrapper {
    background-color: white;
    padding: 27px;
    border-radius: 10px;
}
.options-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
}

.option {
    display: flex;
    margin-bottom: 0.5rem;
    width: 100%;
    font-size: 20px;
    padding: 15px;
    border-radius: 10px;
    /* background: #35A7FF; */
    background: #54C899;
    color: white;
}

.option:hover {
    cursor: pointer;
    background-color: rgb(245, 163, 56);
    color: darkslategrey;
}

.question-container {
    background-color: #38618C;
    color: white;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 20px;
}

.question-container h2 {
    font-size: 20px;
    font-weight: 400;
}

.round-container h2 {
    margin-top: 20px;
    text-align: center;
} 

.h3-chosen-option {
    text-align: center;
    margin-top: 20px;
}

.correct-answer {
    text-align: center;
}
.score-container, .save-score-container {
    background: #f7f7f7b4;
    border-radius: 5px;
    padding: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
}
.navBar-header {
    width: 100%;
    /* background-color: rgb(46, 157, 104); */
    background: -webkit-gradient(linear, left top, right top, from(#f09819), to(#edde5d));
    background: linear-gradient(90deg, #f09819 0%, #edde5d 100%);
    /* background: #F2C438; */
}
.navBar-items{
    padding: 6px;
    display: flex;
    justify-content: center;
}

.logo {
    width: 75px;
}

body {
    background: #3C41A6;
}

main {
    background: #3C41A6;
    height: 100vh;
}

.btn-primary {
    background-color: #54C899!important;
    border-color: #54C899!important;
    border-radius: 50px!important;
}

.container {
    padding: 2rem 1.5rem;
}

a {
    color:#54C899!important;
}

h2 {
    font-size: 27px;
    font-weight: 700;
}

h3 {
    font-size: 23px;
}
